import React, { useState } from 'react';
import './assets/css/HotelDetailsPage.css';

interface Room {
  id: number;
  image: string;
  description: string;
  price: number;
  availability: boolean;
  calendar: boolean[];
}

const HotelOwnerPage: React.FC = () => {
  const [hotelName, setHotelName] = useState<string>('Luxury Hotel');
  const [rooms, setRooms] = useState<Room[]>(
    [...Array(3)].map((_, index) => ({
      id: index,
      image: `https://via.placeholder.com/150?text=Room+${index + 1}`,
      description: `Room ${index + 1}: Beautiful and spacious.`,
      price: 5000 + index * 1000,
      availability: true,
      calendar: Array(30).fill(true), // 30 days of availability
    }))
  );

  const [activeCalendar, setActiveCalendar] = useState<number | null>(null);

  // Update Room Details
  const updateRoom = (index: number, key: keyof Room, value: any) => {
    const updatedRooms = [...rooms];
    updatedRooms[index][key] = value;
    setRooms(updatedRooms);
  };

  // Toggle Specific Date Availability
  const toggleDateAvailability = (roomIndex: number, dateIndex: number) => {
    const updatedRooms = [...rooms];
    updatedRooms[roomIndex].calendar[dateIndex] =
      !updatedRooms[roomIndex].calendar[dateIndex];
    setRooms(updatedRooms);
  };

  // Toggle Calendar Popup
  const toggleCalendar = (index: number) => {
    setActiveCalendar(activeCalendar === index ? null : index);
  };

  // Add New Room
  const addRoom = () => {
    const newRoom: Room = {
      id: rooms.length,
      image: 'https://via.placeholder.com/150?text=New+Room',
      description: 'New room description.',
      price: 4000,
      availability: true,
      calendar: Array(30).fill(true),
    };
    setRooms([...rooms, newRoom]);
  };

  return (
    <div className="hotel-details-page">
      {/* Editable Hotel Name */}
      <header className="hotel-header">
        <input
          type="text"
          value={hotelName}
          onChange={(e) => setHotelName(e.target.value)}
          className="hotel-name-input"
          placeholder="Enter Hotel Name"
        />
      </header>

      {/* Room Sections */}
      <div className="room-sections">
        {rooms.map((room, index) => (
          <div key={room.id} className="room-container">
            {/* Room Image Upload */}
            <div className="room-image">
              <input
                type="file"
                onChange={(e) => {
                  if (e.target.files && e.target.files[0]) {
                    const imageUrl = URL.createObjectURL(e.target.files[0]);
                    updateRoom(index, 'image', imageUrl);
                  }
                }}
              />
              <img
                src={room.image}
                alt={`Room ${index + 1}`}
                className="room-pic"
              />
            </div>

            {/* Room Details */}
            <div className="room-info">
              <textarea
                className="room-description"
                value={room.description}
                onChange={(e) =>
                  updateRoom(index, 'description', e.target.value)
                }
              />
              <div className="room-status">
                <input
                  type="checkbox"
                  checked={room.availability}
                  onChange={(e) =>
                    updateRoom(index, 'availability', e.target.checked)
                  }
                />
                <span className="availability-status">
                  {room.availability ? 'Available' : 'Unavailable'}
                </span>
                <div
                  className="calendar-icon"
                  onClick={() => toggleCalendar(index)}
                >
                  
                </div>
              </div>
              <div className="room-price">
                Price: 
                <input
                  type="number"
                  value={room.price}
                  onChange={(e) =>
                    updateRoom(index, 'price', parseInt(e.target.value) || 0)
                  }
                  className="room-price-input"
                />
              </div>
            </div>

            {/* Calendar Popup */}
            {activeCalendar === index && (
              <div className="calendar-popup">
                <h4>Edit Availability (Next 30 Days)</h4>
                <div className="calendar-grid">
                  {room.calendar.map((isAvailable, dayIndex) => (
                    <div
                      key={dayIndex}
                      className={`calendar-date ${
                        isAvailable ? 'available' : 'unavailable'
                      }`}
                      onClick={() => toggleDateAvailability(index, dayIndex)}
                    >
                      Day {dayIndex + 1}
                    </div>
                  ))}
                </div>
                <button onClick={() => setActiveCalendar(null)}>Close</button>
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Add Room Button */}
      <div className="add-room-container">
        <button className="add-room-button" onClick={addRoom}>
          + Add New Room
        </button>
      </div>
    </div>
  );
};

export default HotelOwnerPage;
