// ImageTextGrid.jsx
import React from 'react';
import { Link } from 'react-router-dom';
import "./assets/css/NewPage.css";
import user from './assets/user-login.jpg';

const ImageTextGrid = () => {
    return (
        <div className="grid-container">
            <Link to="/user-signup" className="grid-item">
                <img src={user} alt="User Signup" className="grid-image" />
                <p className="page-color">User Signup</p>
            </Link>
            <Link to="/companyForm" className="grid-item">
                <img src={user} alt="Company Form" className="grid-image" />
                <p className="page-color">Company Form</p>
            </Link>
            <Link to="/staff-signup" className="grid-item">
                <img src={user} alt="Staff Signup" className="grid-image" />
                <p className="page-color">Staff Signup</p>
            </Link>
            <Link to="/delivery-boy-signup" className="grid-item">
                <img src={user} alt="Delivery Boy Signup" className="grid-image" />
                <p className="page-color">Delivery Boy Signup</p>
            </Link>
        </div>
    );
};

export default ImageTextGrid;
