import React, { useState } from "react";
import "./assets/css/mobilLogin.css";

const MobileLogin: React.FC = () => {
  const [mobile, setMobile] = useState("");
  const [otp, setOtp] = useState("");
  const [step, setStep] = useState(1);
  const [error, setError] = useState("");

  const handleMobileSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (mobile.length !== 10 || isNaN(Number(mobile))) {
      setError("Please enter a valid 10-digit mobile number");
      return;
    }

    setError("");
    setStep(2);
    // Simulate sending OTP (you'd call an API in real projects)
    console.log(`Sending OTP to ${mobile}`);
  };

  const handleOtpSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (otp.length !== 4 || isNaN(Number(otp))) {
      setError("Please enter a valid 4-digit OTP");
      return;
    }

    setError("");
    console.log("OTP Verified!");
    alert("Logged in successfully!");
  };

  return (
    <div className="mobile-login-container">
      <h2>{step === 1 ? "Login with Mobile" : "Enter OTP"}</h2>
      {step === 1 && (
        <form onSubmit={handleMobileSubmit}>
          <div className="form-group">
            <label htmlFor="mobile">Mobile Number</label>
            <input
              type="text"
              id="mobile"
              maxLength={10}
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
              placeholder="Enter mobile number"
            />
          </div>
          {error && <p className="error">{error}</p>}
          <button type="submit" className="btn">Send OTP</button>
        </form>
      )}

      {step === 2 && (
        <form onSubmit={handleOtpSubmit}>
          <div className="form-group">
            <label htmlFor="otp">OTP</label>
            <input
              type="text"
              id="otp"
              maxLength={4}
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
              placeholder="Enter OTP"
            />
          </div>
          {error && <p className="error">{error}</p>}
          <button type="submit" className="btn">Verify OTP</button>
        </form>
      )}
    </div>
  );
};

export default MobileLogin;
