import React, { useState, useEffect } from 'react';
import './assets/css/InstaOrder.css';

// Import images explicitly
import paneerTikka from './assets/css/paneer-tikka.jpg';
import biryani from './assets/css/biryani.jpg';
import masalaDosa from './assets/css/masala-dosa.jpg';
import pasta from './assets/css/pasta.jpg';
import instantFood from './assets/css/instant-food.jpg';
import breakfast from './assets/css/breakfast.jpg';
import lunch from './assets/css/lunch.jpg';
import dinner from './assets/css/dinner.jpg';

const InstaOrder: React.FC = () => {
  const [search, setSearch] = useState('');
  const [cart, setCart] = useState<any[]>([]); // Initialize cart as empty array
  const [isCartOpen, setIsCartOpen] = useState(false); // Manage cart popup visibility

  // Sample dishes data
  const dishes = [
    { id: 1, name: 'Paneer Tikka', price: 200, image: paneerTikka },
    { id: 2, name: 'Paneer Biryani', price: 250, image: biryani },
    { id: 3, name: 'Masala Dosa', price: 150, image: masalaDosa },
    { id: 4, name: 'Veg Pasta', price: 180, image: pasta },
  ];

  const categories = [
    { src: instantFood, title: 'Instant Food' },
    { src: breakfast, title: 'Breakfast' },
    { src: lunch, title: 'Lunch' },
    { src: dinner, title: 'Dinner' },
  ];

  useEffect(() => {
    const savedCart = localStorage.getItem('cart');
    if (savedCart) {
      setCart(JSON.parse(savedCart));
    }
  }, []);

  useEffect(() => {
    if (cart.length > 0) {
      localStorage.setItem('cart', JSON.stringify(cart));
    }
  }, [cart]);

  const addToCart = (dish: any) => {
    setCart([...cart, { ...dish, quantity: 1 }]);
  };

  const incrementQuantity = (id: number) => {
    setCart(cart.map((item) =>
      item.id === id ? { ...item, quantity: item.quantity + 1 } : item
    ));
  };

  const decrementQuantity = (id: number) => {
    const updatedCart = cart
      .map((item) =>
        item.id === id ? { ...item, quantity: item.quantity - 1 } : item
      )
      .filter((item) => item.quantity > 0); // Remove items with quantity 0
    setCart(updatedCart);
  };

  const getCartItem = (id: number) => cart.find((item) => item.id === id);

  const toggleCartPopup = () => {
    setIsCartOpen(!isCartOpen); // Toggle the cart popup visibility
  };

  // Calculate Subtotal, GST, Platform Fee (5%), and Total
  const subtotal = cart.reduce((total, item) => total + item.price * item.quantity, 0);
  const gst = subtotal * 0.18; // 18% GST
  const platformFee = subtotal * 0.05; // 5% Platform Fee
  const totalPrice = subtotal + gst + platformFee;

  return (
    <div className="insta-order">
      {/* Header Section */}
      <header className="insta-order-header">
        <div className="logo">Restaurant Name</div>
        <div className="search-container8">
          <input
            type="text"
            className="search-bar"
            placeholder="Search for dishes or cuisines..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
          <button className="search-btn">Search</button>
        </div>
        <div className="view-cart-button-container">
          <button onClick={toggleCartPopup} className="view-cart-buttons">
            <i className="fas fa-shopping-cart"></i> View Cart ({cart.length})
          </button>
        </div>
      </header>

      {/* Banner Section */}
      <div className="banner-section">
        <h1>Delight in Every Bite</h1>
        <p>Order your favorite meals, delivered fresh and fast to your doorstep.</p>
        <button className="cta-btn">Order Now</button>
      </div>

      {/* Categories Section */}
      <div className="food-types-section">
        <h2>Explore Categories</h2>
        <div className="food-types">
          {categories.map((item, index) => (
            <div className="food-type-item" key={index}>
              <img src={item.src} alt={item.title} />
              <h3>{item.title}</h3>
            </div>
          ))}
        </div>
      </div>

      {/* Popular Dishes Section */}
      <div className="popular-dishes-section">
        <h2>Popular Dishes</h2>
        <div className="popular-dishes">
          {dishes.map((dish, index) => {
            const cartItem = getCartItem(dish.id);
            return (
              <div className="dish-card" key={index}>
                <img src={dish.image} alt={dish.name} />
                <h4>{dish.name}</h4>
                <p>{dish.price}</p>
                <div className="button-container">
                  {!cartItem ? (
                    <button
                      onClick={() => addToCart(dish)}
                      className="add-to-cart-btn"
                    >
                      Add to Cart
                    </button>
                  ) : (
                    <div className="quantity-controls">
                      <button onClick={() => decrementQuantity(dish.id)}>-</button>
                      <span>{cartItem.quantity}</span>
                      <button onClick={() => incrementQuantity(dish.id)}>+</button>
                    </div>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* Cart Popup (right side) */}
      {isCartOpen && (
        <div className="cart-popup">
          <div className="cart-popup-content">
            <h1>My Cart</h1>
            {cart.length > 0 ? (
              <ul className="cart-list">
                {cart.map((item: any) => (
                  <li key={item.id} className="cart-item">
                    <img src={item.image} alt={item.name} className="cart-item-image" />
                    <div className="cart-item-details">
                      <p>{item.name}</p>
                      <p>{item.price} x {item.quantity}</p>
                      <div className="quantity-controls">
                        <button
                          className="quantity-button"
                          onClick={() => decrementQuantity(item.id)}
                        >
                          -
                        </button>
                        <span className="quantity-display">{item.quantity}</span>
                        <button
                          className="quantity-button"
                          onClick={() => incrementQuantity(item.id)}
                        >
                          +
                        </button>
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            ) : (
              <p>Your cart is empty.</p>
            )}
            <div className="cart-summary">
              <p>Subtotal: {subtotal.toFixed(2)}</p>
              <p>GST (18%): {gst.toFixed(2)}</p>
              <p>Platform Fee (5%): {platformFee.toFixed(2)}</p>
              <div className="cart-total">
                <h2>Total Amount: {totalPrice.toFixed(2)}</h2>
              </div>
            </div>
            <div className="cart-actions">
              <button className="checkout-button">Proceed to Checkout</button>
              <button className="close-button" onClick={toggleCartPopup}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default InstaOrder;
